.SwitchTheme_slider__in4ol {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #050000;
  transition: 0.4s;
}

.SwitchTheme_slider__in4ol:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: #7e7777;
  transition: 0.4s;
}

input:checked + .SwitchTheme_slider__in4ol {
  background-color: #ffffff;
}

input:focus + .SwitchTheme_slider__in4ol {
  box-shadow: 0 0 1px black;
}

input:checked + .SwitchTheme_slider__in4ol:before {
  transform: translateX(26px);
}

/* Rounded sliders */
.SwitchTheme_slider__in4ol {
  border-radius: 34px;
}

.SwitchTheme_slider__in4ol:before {
  border-radius: 50%;
}

